* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.country-list {
  padding: 0px;
}

.country-list__name {
  margin-left: 20px;
  font-weight: 500;
}

.country-list__flag {
  width: 40px;
  height: auto;
  display: block;
}

.country-list__item {
  display: flex;
  align-items: center;
}
